import { Editor, Node, Text, Path, Operation, Range, Transforms, Element, Descendant } from 'slate'
import { DomEditor } from '../dom-editor'
import { IDomEditor } from '../..'
import { EDITOR_TO_SELECTION, NODE_TO_KEY } from '../../utils/weak-maps'
import node2html from '../../to-html/node2html'
import { genElemId } from '../../render/helper'
import { Key } from '../../utils/key'
import $, { DOMElement, NodeType } from '../../utils/dom'
import { findCurrentLineRange } from '../../utils/line'
import { ElementWithId } from '../interface'
import { PARSE_ELEM_HTML_CONF, TEXT_TAGS } from '../../parse-html/index'
import parseElemHtml from '../../parse-html/parse-elem-html'
import { htmlToContent } from '../../create/helper'

const IGNORE_TAGS = new Set([
  'doctype',
  '!doctype',
  'meta',
  'script',
  'style',
  'link',
  'frame',
  'iframe',
  'title',
  'svg', // TODO ææ¶å¿½ç¥
])

export const withContent = <T extends Editor>(editor: T) => {
  const e = editor as T & IDomEditor
  const { onChange, insertText, apply, deleteBackward } = e

  e.insertText = (text: string) => {
    const { readOnly } = e.getConfig()
    if (readOnly) return

    insertText(text)
  }

  // éå apply æ¹æ³
  // apply æ¹æ³éå¸¸éè¦ï¼å®æç»æ§è¡ operation https://docs.slatejs.org/concepts/05-operations
  // operation çæ¥å£å®ä¹åè slate src/interfaces/operation.ts
  e.apply = (op: Operation) => {
    const matches: [Path, Key][] = []

    switch (op.type) {
      case 'insert_text':
      case 'remove_text':
      case 'set_node': {
        for (const [node, path] of Editor.levels(e, { at: op.path })) {
          // å¨å½åèç¹å¯»æ¾
          const key = DomEditor.findKey(e, node)
          matches.push([path, key])
        }
        break
      }

      case 'insert_node':
      case 'remove_node':
      case 'merge_node':
      case 'split_node': {
        for (const [node, path] of Editor.levels(e, { at: Path.parent(op.path) })) {
          // å¨ç¶èç¹å¯»æ¾
          const key = DomEditor.findKey(e, node)
          matches.push([path, key])
        }
        break
      }

      case 'move_node': {
        for (const [node, path] of Editor.levels(e, {
          at: Path.common(Path.parent(op.path), Path.parent(op.newPath)),
        })) {
          const key = DomEditor.findKey(e, node)
          matches.push([path, key])
        }
        break
      }
    }

    // æ§è¡åæ¬ç apply - éè¦ï¼ï¼ï¼
    apply(op)

    // ç»å® node å key
    for (const [path, key] of matches) {
      const [node] = Editor.node(e, path)
      NODE_TO_KEY.set(node, key)
    }
  }

  e.deleteBackward = unit => {
    if (unit !== 'line') {
      return deleteBackward(unit)
    }

    if (editor.selection && Range.isCollapsed(editor.selection)) {
      const parentBlockEntry = Editor.above(editor, {
        match: n => Editor.isBlock(editor, n),
        at: editor.selection,
      })

      if (parentBlockEntry) {
        const [, parentBlockPath] = parentBlockEntry
        const parentElementRange = Editor.range(editor, parentBlockPath, editor.selection.anchor)

        const currentLineRange = findCurrentLineRange(e, parentElementRange)

        if (!Range.isCollapsed(currentLineRange)) {
          Transforms.delete(editor, { at: currentLineRange })
        }
      }
    }
  }

  // éå onchange API
  e.onChange = () => {
    // è®°å½å½åéåº
    const { selection } = e
    if (selection != null) {
      EDITOR_TO_SELECTION.set(e, selection)
    }

    // è§¦åéç½®ç change äºä»¶
    e.emit('change')

    onChange()
  }

  // tab
  e.handleTab = () => {
    e.insertText('    ')
  }

  // è·å html ï¼å»æäºæ ¼å¼å 2021.12.10ï¼
  e.getHtml = (): string => {
    const { children = [] } = e
    const html = children.map(child => node2html(child, e)).join('')
    return html
  }

  // è·å text
  e.getText = (): string => {
    const { children = [] } = e
    return children.map(child => Node.string(child)).join('\n')
  }

  // è·åéåºæå­
  e.getSelectionText = (): string => {
    const { selection } = e
    if (selection == null) return ''
    return Editor.string(editor, selection)
  }

  // æ ¹æ® type è·å elems
  e.getElemsByType = (type: string, isPrefix = false): ElementWithId[] => {
    const elems: ElementWithId[] = []

    // è·å editor ææ nodes
    const nodeEntries = Editor.nodes(e, {
      at: [],
      universal: true,
    })
    for (let nodeEntry of nodeEntries) {
      const [node] = nodeEntry
      if (Element.isElement(node)) {
        // å¤æ­ type ï¼åç¼ or å¨ç­ï¼
        let flag = isPrefix ? node.type.indexOf(type) >= 0 : node.type === type
        if (flag) {
          const key = DomEditor.findKey(e, node)
          const id = genElemId(key.id)

          // node + id
          elems.push({
            ...node,
            id,
          })
        }
      }
    }

    return elems
  }

  // æ ¹æ® type åç¼ï¼è·å elems
  e.getElemsByTypePrefix = (typePrefix: string): ElementWithId[] => {
    return e.getElemsByType(typePrefix, true)
  }

  /**
   * å¤æ­ editor æ¯å¦ä¸ºç©ºï¼åªæä¸ä¸ªç©º paragraphï¼
   */
  e.isEmpty = () => {
    const { children = [] } = e
    if (children.length > 1) return false // >1 ä¸ªé¡¶çº§èç¹

    const firstNode = children[0]
    if (firstNode == null) return true // editor.children ç©ºæ°ç»

    if (Element.isElement(firstNode) && firstNode.type === 'paragraph') {
      const { children: texts = [] } = firstNode
      if (texts.length > 1) return false // >1 text node

      const t = texts[0]
      if (t == null) return true // æ  text èç¹

      if (Text.isText(t) && t.text === '') return true // åªæä¸ä¸ª text ä¸æ¯ç©ºå­ç¬¦ä¸²
    }

    return false
  }

  /**
   * æ¸ç©ºåå®¹
   */
  e.clear = () => {
    const initialEditorValue: Node[] = [
      {
        type: 'paragraph',
        children: [{ text: '' }],
      },
    ]
    Transforms.removeNodes(e, {
      at: {
        anchor: Editor.start(e, []),
        focus: Editor.end(e, []),
      },
      mode: 'highest',
    })

    if (e.children.length === 0) {
      Transforms.insertNodes(e, initialEditorValue)
    }
  }

  e.getParentNode = (node: Node) => {
    return DomEditor.getParentNode(e, node)
  }

  /**
   * æå¥ html ï¼ä¸ä¿è¯è¯­ä¹å®å¨æ­£ç¡®ï¼ï¼ç¨äºç²è´´
   * @param html html string
   * @param isRecursive æ¯å¦éå½è°ç¨ï¼åé¨ä½¿ç¨ï¼ä½¿ç¨èä¸è¦ä¼ åï¼
   */
  e.dangerouslyInsertHtml = (html: string = '', isRecursive = false) => {
    if (!html) return

    // ------------- æ html è½¬æ¢ä¸º DOM nodes -------------
    const div = document.createElement('div')
    div.innerHTML = html
    let domNodes = Array.from(div.childNodes)

    // è¿æ»¤ä¸ä¸ï¼åªä¿ç elem å text ï¼å¹¶å´æä¸äºæ ç¨æ ç­¾ï¼å¦ style script ç­ï¼
    domNodes = domNodes.filter(n => {
      const { nodeType, nodeName } = n
      // Text Node
      if (nodeType === NodeType.TEXT_NODE) return true

      // Element Node
      if (nodeType === NodeType.ELEMENT_NODE) {
        // è¿æ»¤æå¿½ç¥ç tag
        if (IGNORE_TAGS.has(nodeName.toLowerCase())) return false
        else return true
      }
      return false
    })
    if (domNodes.length === 0) return

    // ------------- æ DOM nodes è½¬æ¢ä¸º slate nodes ï¼å¹¶æå¥å°ç¼è¾å¨ -------------

    const { selection } = e
    if (selection == null) return
    let curEmptyParagraphPath: Path | null = null

    // æ¯å¦å½åéä¸­äºä¸ä¸ªç©º p ï¼å¦ææ¯ï¼åé¢ä¼å æï¼
    // éå½è°ç¨æ¶ä¸å¤æ­
    if (DomEditor.isSelectedEmptyParagraph(e) && !isRecursive) {
      const { focus } = selection
      curEmptyParagraphPath = [focus.path[0]] // åªè®°å½é¡¶çº§ path å³å¯
    }

    div.setAttribute('hidden', 'true')
    document.body.appendChild(div)

    let insertedElemNum = 0 // è®°å½æå¥ elem çæ°é ( textNode ä¸ç® )
    domNodes.forEach(n => {
      const { nodeType, nodeName, textContent = '' } = n

      // ------ Text node ------
      if (nodeType === NodeType.TEXT_NODE) {
        if (!textContent || !textContent.trim()) return // æ åå®¹ç Text

        // æå¥ææ¬
        //ãæ³¨æãinsertNode å insertText æåºå«ï¼åèä¼ç»§æ¿åæ å¤çææ¬æ ·å¼ï¼å¦å ç²ï¼ï¼åèä¼å å¥çº¯ææ¬ï¼æ æ ·å¼ï¼
        e.insertNode({ text: textContent })
        return
      }

      // ------ Element Node ------
      if (nodeName === 'BR') {
        e.insertText('\n') // æ¢è¡
        return
      }

      // å¤æ­å½åç el æ¯å¦æ¯å¯è¯å«ç tag
      const el = n as DOMElement
      let isParseMatch = false
      if (TEXT_TAGS.includes(nodeName.toLowerCase())) {
        // text elemï¼å¦ <span>
        isParseMatch = true
      } else {
        for (let selector in PARSE_ELEM_HTML_CONF) {
          if (el.matches(selector)) {
            // æ®é elemï¼å¦ <p> <a> ç­ï¼é text elemï¼
            isParseMatch = true
            break
          }
        }
      }

      // å¹éä¸äºï¼åçæ slate elem å¹¶æå¥
      if (isParseMatch) {
        // çæå¹¶æå¥
        const $el = $(el)
        const newElem = parseElemHtml($el, e) as Element

        if (e.isInline(newElem)) {
          // inline elem ç´æ¥æå¥
          e.insertNode(newElem)

          // link ç¹æ®å¤çï¼å¦ååé¢æå¥çæå­å¨é½å¨ a éé¢ issue#4573
          if (newElem.type === 'link') e.insertFragment([{ text: '' }])
        } else {
          // block elem ï¼å¦èµ·ä¸è¡æå¥ ââ éè¦
          Transforms.insertNodes(e, newElem, { mode: 'highest' })
        }
        insertedElemNum++ // è®°å½æ°é

        // å¦æå½åéä¸­ void node ï¼åéåºç§»å¨ä¸ä¸
        if (DomEditor.isSelectedVoidNode(e)) e.move(1)

        return
      }

      // æ²¡æå¹éä¸ï¼å¦ div ï¼
      const display = window.getComputedStyle(el).display
      if (!DomEditor.isSelectedEmptyParagraph(e)) {
        // å½åä¸æ¯ç©ºè¡ï¼ä¸ é inline - åæ¢è¡
        if (display.indexOf('inline') < 0) e.insertBreak()
      }
      e.dangerouslyInsertHtml(el.innerHTML, true) // ç»§ç»­æå¥å­åå®¹
    })

    // å é¤ç¬¬ä¸ä¸ªç©ºè¡
    if (insertedElemNum && curEmptyParagraphPath) {
      if (DomEditor.isEmptyPath(e, curEmptyParagraphPath)) {
        Transforms.removeNodes(e, { at: curEmptyParagraphPath })
      }
    }

    div.remove() // ç²è´´å®äºï¼ç§»é¤ div
  }

  /**
   * éç½® HTML åå®¹
   * @param html html string
   */
  e.setHtml = (html: string = '') => {
    // è®°å½ç¼è¾å¨å½åç¶æ
    const isEditorDisabled = e.isDisabled()
    const isEditorFocused = e.isFocused()
    const editorSelectionStr = JSON.stringify(e.selection)

    // å é¤å½ååå®¹
    e.enable()
    e.focus()
    e.select([])
    e.deleteFragment()
    Transforms.setNodes(e, { type: 'paragraph' }, { mode: 'highest' })

    // è®¾ç½®æ°åå®¹
    const newContent = htmlToContent(e, html)
    Transforms.insertFragment(e, newContent)

    // æ¢å¤ç¼è¾å¨ç¶æåéåº
    if (!isEditorFocused) {
      e.deselect()
      e.blur()
    }
    if (isEditorDisabled) {
      e.deselect()
      e.disable()
    }
    if (e.isFocused()) {
      try {
        e.select(JSON.parse(editorSelectionStr)) // éä¸­åæ¥çä½ç½®
      } catch (ex) {
        e.select(Editor.start(e, [])) // éä¸­å¼å§
      }
    }
  }

  return e
}
