import { Descendant } from 'slate'
import { IDomEditor } from '../editor/interface'
import parseElemHtml from '../parse-html/parse-elem-html'
import $, { DOMElement } from '../utils/dom'

function isRepeatedCreate(
  editor: IDomEditor,
  attrKey: string,
  selector: string | DOMElement
): boolean {
  // @ts-ignore
  const $elem = $(selector)
  if ($elem.attr(attrKey)) {
    return true // æå±æ§ï¼è¯´æå·²ç»åå»ºè¿
  }

  // è³æ­¤ï¼è¯´ææªåå»ºè¿ï¼åè®°å½
  $elem.attr(attrKey, 'true')

  // éæ¯æ¶å é¤å±æ§
  editor.on('destroyed', () => {
    $elem.removeAttr(attrKey)
  })

  return false
}

/**
 * æ£æ¥æ¯å¦éå¤åå»º textarea
 */
export function isRepeatedCreateTextarea(
  editor: IDomEditor,
  selector: string | DOMElement
): boolean {
  return isRepeatedCreate(editor, 'data-w-e-textarea', selector)
}

/**
 * æ£æ¥æ¯å¦éå¤åå»º toolbar
 */
export function isRepeatedCreateToolbar(
  editor: IDomEditor,
  selector: string | DOMElement
): boolean {
  return isRepeatedCreate(editor, 'data-w-e-toolbar', selector)
}

/**
 * çæé»è®¤ content
 */
export function genDefaultContent() {
  return [
    {
      type: 'paragraph',
      children: [{ text: '' }],
    },
  ]
}

/**
 * html å­ç¬¦ä¸² -> content
 * @param editor editor
 * @param html html å­ç¬¦ä¸²
 */
export function htmlToContent(editor: IDomEditor, html: string = ''): Descendant[] {
  // ç©ºç½åå®¹
  if (html === '') html = '<p><br></p>'

  // é HTML æ ¼å¼ï¼ææ¬æ ¼å¼ï¼ç¨ <p> åè£¹
  if (html.indexOf('<') !== 0) {
    html = html
      .split(/\n/)
      .map(line => `<p>${line}</p>`)
      .join('')
  }

  const $content = $(`<div>${html}</div>`)
  return Array.from($content.children()).map(child => {
    const $child = $(child)
    return parseElemHtml($child, editor)
  })
}
